.auth__main{
    align-items: center;
    background-color: $primary;
    display: flex;
    justify-content: center;
    margin: 0px;
    height: 100vh;
    width: 100vw;
}
.auth__box-container{
    background-color: white;
    box-shadow: 10px 10px 8px $dark-grey;
    border-radius: 10px;
    padding: 20px;
    //width: 250px;
    height: 300px;
    width: 500px;
}

.auth__title{
    color: darken($color:$primary, $amount: 20);
    margin-bottom: 20px;
    margin-top: 10px;
}

.auth__input{

    color: $dark-grey;
    border: 0px;
    border-bottom: 1px solid $light-grey;
    font-size: 16px;
    margin-bottom: 10px;
    height: 20px;
    width: 100%;
    transition: border-bottom .3s ease;
    
}

.auth__title-register{

    color: $dark-grey;
    border: 0px;
    border-bottom: 1px solid $light-grey;
    font-size: 16px;
    margin-bottom: 10px;
    height: 20px;
    width: 100%;
    transition: border-bottom .3s ease;
    padding: 3px;
}

.auth__input:focus{
    border-bottom: 1px solid $primary;
    outline: none;
}

.auth__social-network{
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
}

