.journal__main-content{
    display: flex;
}

.journal__sidebar{
    background-color: darken($color: $blue-cover, $amount: 40);
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    width: 450px;
}

.journal__sidebar-navbar{
    display: flex;
    justify-content: space-between;
}

.journal__sidebar-navbar h3 {
    font-weight: lighter;
}

.journal__new-entry{
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;
    width: 100%;
    transition: color .3s ease ;
    &:hover{
        color: rgb(87, 164, 194);
    }

}

.journal__entries{
    flex: 1 1 auto;
    margin-top: 30px;
    overflow-y: scroll hidden;

}

.journal__entry{
    background-color: darken($color: $blue-cover, $amount: 30);
    cursor: pointer;
    border-radius: 15px;
    color: darken($color: white, $amount: 20);
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

.journal__entry-picture{
    border-radius: 100%;
    background-size: cover;
    height: 75px;
    margin-top: 10px;
    margin-left: 5px;
    width: 75px;
}

.journal__entry-body{
    padding: 10px;
}

.journal__entry-title{
    font-size: 18px;
    font-weight: bold;
}

.journal__entry-content{
    font-size: 13px;
}

.journal__entry-date-box{
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5x;
    margin-right: 5px;
}
